.main{
    width: 1300px;
    height: 350px;
    margin: 0 auto; 
    padding-top: 50px;
    position: relative;
}

.main input{
    width: 1200px;
    height: 45px;
    border-radius: 32px;
    position: absolute;
    left: 50%;
    bottom: 0px;
    transform: translateX(-50%);
    z-index: 1;
    background: #fff;
    text-indent: 30px;
}
.main button{
    width: 100px;
    height: 45px;
    position: absolute;
    bottom: 0px;
    right: 50px;   
    border-radius: 32px;
    background: #08BCD6;
    z-index: 6;
}

