.header{
    width: 100%;
    height: 65px;
    background: #032541;
    position: fixed;
    z-index: 10;
}
.header .inner{
    width: 1300px;
    height: 65px;
    padding: 0 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
}
.header .inner .left_box{
    display: flex;
    gap: 30px;
    align-items: center;
}
.header .inner .left_box ul{
    display: flex;
    gap: 30px;
    align-items: center;
    color: aliceblue;
}
.header .inner .logo{
    width: 160px;
    height: 30px;
}
.header .inner .logo img{
    width: 100%;
    height: 100%;
    transition: none !important;
    border-radius: 0 !important;
    transform: none !important;
}
.right_box ul{
    display: flex;
    align-items: center;
    color: aliceblue;
    gap: 30px;
}
.ko{
    border: 1px solid #ffffff;
    padding: 3px 5px;
    border-radius: 5px;
}
.ss{
    z-index: 20;
    position: fixed;
}
.Login{
    z-index: 50;
    width: 100% !important;
    height: 100vh !important;
    background: #ffffff;
    margin: 0 auto  ;
    border: 1px solid #000;
}
.Login .inner{
    padding-top: 100px;
    display: flex;
    flex-direction: column;
    gap: 50px;
    width: 1300px;
    margin: 0 auto;
    /* border: 1px solid #ff0000; */
}
.Login .inner input{
    border: 1px solid #000;
    width: 500px;
    height: 80px !important;
    padding: 15px;
}
.Login .inner button{
    border: 1px solid #000;
    padding: 10px;
}
.Login .inner button:hover{
    background: #032541;
    color: #ffffff;
    transition: all 0.32s ease-in-out;
}
a{
    color: #ffffff;
}