.main_img{
  width: 1300px;
  height: 350px;
  background: url(../images/Frame\ 6.jpg);
  z-index: 1;
}
.trand{
  background: #032541;  
  color: aliceblue;
  padding: 20px;
  padding-top: 0;
}
.trand h2{
  color: aliceblue;
}
.footer{
  width: 100%;
  background: #032541;
  padding: 50px 0;
}
.footer .inner{
  width: 1300px;
  margin: 0 auto ;
  display: flex;
  justify-content: center;
  gap: 30px;
  align-items: center;
}
.footer .inner .logo{
  display: flex;
  gap: 10px;
  align-items: center;

}
.footer .inner .logo img{
  width: 150px !important;
  height: 100px !important;
  border-radius: 0 !important;
  transform: none !important;
}
.footer .inner .logo button{
  border: 1px solid #ffffff;
  border-radius: 16px ;
  width: 150px;
  height: 50px;
  color: #ffffff;
}
.footer .inner ul{
  display: flex;
  gap: 20px;

}
.footer .inner ul li{
  color: aliceblue;
}
.poster{
  z-index: 1 !important;
}